.profile_photo {
    padding-top: 13px;
    margin-top: 22px;
    vertical-align: top;
    border-radius: 4px;
    content: "";
    max-width: 230px;
    background-color: #333336 !important;
    color: white;
    min-height: 270px;
  }
  .profile_photo img {
    margin: 10px 14px 14px 14px;
  
    margin-bottom: 10px;
    margin-top: 0px;
    cursor: pointer;
  }
  .flex_profile_photo {
    max-width: 230px;
    display: flex;
    justify-content: center;
    margin-top: 17px;
    align-items: center;
  }
  .bt1_photo {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-right: 10px;
    font-size: 12.5px;
    background-color: #444446;
    border: none;
    outline: none;
    padding: 7px 36px 8px 36px;
    margin-top: -20px;
    border-radius: 4px;
    color: #ccc;
    font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Helvetica Neue",
      Geneva, "Noto Sans Armenian", "Noto Sans Bengali", "Noto Sans Cherokee",
      "Noto Sans Devanagari", "Noto Sans Ethiopic", "Noto Sans Georgian",
      "Noto Sans Hebrew", "Noto Sans Kannada", "Noto Sans Khmer", "Noto Sans Lao",
      "Noto Sans Osmanya", "Noto Sans Tamil", "Noto Sans Telugu", "Noto Sans Thai",
      sans-serif, arial, Tahoma, verdana;
  }
  .bt1_photo:hover {
    color: rgba(204, 204, 204, 0.746);
    background-color: #444446c7;
  }
  .bt2_photo {
    margin-top: -20px;
    text-align: center;
    padding: 7px 13px 8px 13px;
    color: #ccc;
    cursor: pointer;
    text-align: center;
    background: 0 0;
    outline: none;
    background-color: #444446;
    border: 0;
    border-radius: 4px;
  }
  .bt2_photo:hover {
    color: rgba(204, 204, 204, 0.746);
    background-color: #444446c7;
  }