.header_about_me {
    margin-left:300px;
  }
  .header_aboutMe_flex {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    padding: 8px;
    padding-right: 0px;
  }
  .header_aboutMe_flex:hover {
    background-color: rgba(119, 119, 119, 0.185);
  }
  .header_aboutMe_flex h1 {
    font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Helvetica Neue",
      Geneva, "Noto Sans Armenian", "Noto Sans Bengali", "Noto Sans Cherokee",
      "Noto Sans Devanagari", "Noto Sans Ethiopic", "Noto Sans Georgian",
      "Noto Sans Hebrew", "Noto Sans Kannada", "Noto Sans Khmer", "Noto Sans Lao",
      "Noto Sans Osmanya", "Noto Sans Tamil", "Noto Sans Telugu", "Noto Sans Thai",
      sans-serif, arial, Tahoma, verdana;
    color: #fff !important;
    font-size: 12.5px;
    font-weight: 700;
    -webkit-font-smoothing: antialiased;
    white-space: nowrap;
  }
  .header_aboutMe_flex img{
    width: 30px;
  }
  .me {
    border-radius: 50%;
    margin-left: 15px;
    margin-right: auto;
  }
  .arrow {
    transform: rotate(90deg);
    margin-left: 5px;
    
  }