.sidebar_nav a{
    margin-bottom: 5px;
    font-family: -apple-system,BlinkMacSystemFont,'Roboto','Helvetica Neue',Geneva,"Noto Sans Armenian","Noto Sans Bengali","Noto Sans Cherokee","Noto Sans Devanagari","Noto Sans Ethiopic","Noto Sans Georgian","Noto Sans Hebrew","Noto Sans Kannada","Noto Sans Khmer","Noto Sans Lao","Noto Sans Osmanya","Noto Sans Tamil","Noto Sans Telugu","Noto Sans Thai",sans-serif,arial,Tahoma,verdana;
    color: #777!important;
    cursor: pointer;
    height: 28px;
    line-height: 27px;
    font-size: 12.5px;
    overflow: hidden;
    text-overflow: ellipsis;
    
}


.palka_nav{
    margin-top: 9px;
    padding-top: 9px;
    border-top: 1px solid #333336;
}
.li_domik{
    margin-left: 10px;
    
}
.Photos_nav svg{
    margin-top: -2px;
}
.Photos_nav{
    padding-right: 40px;
    cursor: pointer;
    display: flex;
    align-items: center;
    align-self: start;
    height: 28px;
    padding-left: 10px;
    
}
.Photos_nav:hover{
    background-color: rgba(119, 119, 119, 0.239);
}
.Photos_nav img{
    max-width: 18%;
}
.fix_home_nav {
    display: flex;
}
.moff{
    transform: translateX(4px);
}