.wrapper_profile_about_top {
    max-width: 495px;
    margin: 0 auto;
    min-height: 60px;
  }
  .profile_about_name {
    display: flex;
  }
  .profile_about_name h1 {
    color: rgb(255, 255, 255);
    direction: ltr;
    display: block;
    font-family: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans",
      "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali",
      "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic",
      "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada",
      "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil",
      "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
    font-size: 19px;
    font-weight: 400;
    line-height: 25px;
    overflow-x: hidden;
    overflow-y: hidden;
    padding-left: 1px;
    text-align: left;
    text-overflow: ellipsis;
    margin-top: 10px;
    -webkit-font-smoothing: subpixel-antialiased;
  }
  
  .profile_about_name h1 img {
    margin-left: 5px;
  }
  .wrapper_online_profile_top {
    display: flex;
    justify-content: space-between;
    text-align: center;
    align-items: center;
  }
  .profile_about_online h1 {
    margin-top: 10px;
    color: rgb(147, 147, 147);
    direction: ltr;
    display: block;
    font-family: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans",
      "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali",
      "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic",
      "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada",
      "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil",
      "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
    font-size: 12.5px;
    font-weight: 400;
    line-height: 18px;
    text-align: center;
    -webkit-font-smoothing: subpixel-antialiased;
  }
  .wrapper_profile_about_top h2 {
    margin-top: 0px;
    color: rgb(147, 147, 147);
    direction: ltr;
    display: block;
    font-family: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans",
      "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali",
      "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic",
      "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada",
      "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil",
      "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
    font-size: 12.5px;
    font-weight: 400;
  
    text-align: left;
    padding: 4px;
    margin-left: -4px;
    margin-bottom: -4px;
    -webkit-font-smoothing: subpixel-antialiased;
  }
  .wrapper_profile_about_top h2:hover {
    cursor: pointer;
    background-color: rgba(119, 119, 119, 0.239);
  }
  