.wrapper_search {
    display: flex;
    align-items: center;
    margin-right: 30px;
  }
  .wrapper_search input {
    padding-left: 29px;
    padding-right: 33px;
    height: 28px;
    border: none;
    border-radius: 14px;
    background-color: #444446!important;
    background-repeat: no-repeat;
    background-position: left;
    background-size: 18px;
    background-position-x: 5px;
    outline: none;
    color: #ffffff;
    background-image: url('../../../Assets/Images/search.svg');
  }
  
  
  .wrapper_search img {
    height: 15px;
    margin-right: -30px;
    background-color: rgba(119, 119, 119, 0.082);
  }